<template>
  <div id="app">
		<router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
    	
    };
  },
  created() {

  },
  mounted(){
  	// 检测本地是否有钱包信息,有的话跳创建钱包
	 if(localStorage.getItem('walletArr')){
			this.$router.push('/home');
	 }
  },
  methods: {},
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}
</style>



