import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    	//登录
    	{
	    	path: '',
      	name:'Wallet',
      	meta:{
      		title:'币多多生态链钱包'
      	},
		    component: function (resolve) {
		      require(['../template/wallet'], resolve)
		    },
		    	children:[
			      {
					    	path: '/wallet',
					    	alias:'',
				      	name:'Wallet',
				      	meta:{
				      		title:'币多多生态链钱包'
				      	},
						    component: function (resolve) {
						      require(['../template/wallet/index'], resolve)
						    },
				    },
			    	{
				    	path: '/creatWallet',
			      	name:'CreatWallet',
			      	meta:{
			      		title:'创建钱包'
			      	},
					    component: function(resolve){
					      require(['../template/wallet/creatWallet'], resolve)
					    },
				  	},{
				    	path: '/importWallet',
			      	name:'ImportWallet',
			      	meta:{
			      		title:'导入钱包'
			      	},
					    component: function(resolve){
					      require(['../template/wallet/importWallet'], resolve)
					    },
				  	}]
	    },
		  {
		    	path: '/home',
			    component: function (resolve) {
			      require(['../template/home'], resolve)
			    },
			    children:[
			    	{
					    	path: '/home',
				      	name:'Home',
				      	meta:{
				      		title:'首页'
				      	},
						    component: function (resolve) {
						      require(['../template/home/index'], resolve)
						    },
				    },
				    {
					    	path: '/assets',
				      	name:'Assets',
				      	meta:{
				      		title:'添加资产'
				      	},
						    component: function (resolve) {
						      require(['../template/home/assets'], resolve)
						    },
				    },
				    {
					    	path: '/homeDetails',
				      	name:'HomeDetails',
				      	meta:{
				      		title:'详情'
				      	},
						    component: function (resolve) {
						      require(['../template/home/homeDetails'], resolve)
						    },
				    },
				    {
					    	path: '/transferAccounts',
				      	name:'TransferAccounts',
				      	meta:{
				      		title:'转账'
				      	},
						    component: function (resolve) {
						      require(['../template/home/transferAccounts'], resolve)
						    },
				    },
				     {
					    	path: '/qrCode',
				      	name:'QrCode',
				      	meta:{
				      		title:'扫一扫'
				      	},
						    component: function (resolve) {
						      require(['../template/home/qrCode'], resolve)
						    },
				    },
				    {
					    	path: '/quotation',
				      	name:'Quotation',
				      	meta:{
				      		keepAlive:true,
				      		title:'行情'
				      	},
						    component: function (resolve) {
						      require(['../template/quotation/index'], resolve)
						    },
				    },
				    {
					    	path: '/find',
				      	name:'Find',
				      	meta:{
				      		keepAlive:true,
				      		title:'发现'
				      	},
						    component: function (resolve) {
						      require(['../template/find/index'], resolve)
						    },
				    },
				    {
					    	path: '/my',
				      	name:'My',
				      	meta:{
				      		keepAlive:true,
				      		title:'我的'
				      	},
						    component: function (resolve) {
						      require(['../template/my/index'], resolve)
						    },
				    },
				    //walletManage
				    {
					    	path: '/walletManage',
				      	name:'WalletManage',
				      	meta:{
				      		title:'钱包管理'
				      	},
						    component: function (resolve) {
						      require(['../template/my/walletManage'], resolve)
						    },
				    },
				    //myWallet
				    {
					    	path: '/myWallet',
				      	name:'MyWallet',
				      	meta:{
				      		title:'我的钱包'
				      	},
						    component: function (resolve) {
						      require(['../template/my/myWallet'], resolve)
						    },
				    },
				     //editPassword
						{
					    	path: '/editPassword',
				      	name:'EditPassword',
				      	meta:{
				      		title:'修改密码'
				      	},
						    component: function (resolve) {
						      require(['../template/my/editPassword'], resolve)
						    },
				    },
				    //walletBackups
						{
					    	path: '/walletBackups',
				      	name:'WalletBackups',
				      	meta:{
				      		title:'钱包备份'
				      	},
						    component: function (resolve) {
						      require(['../template/my/walletBackups'], resolve)
						    },
				    },
				    //ExportKey
				    {
					    	path: '/exportKey',
				      	name:'ExportKey',
				      	meta:{
				      		title:'导出私钥'
				      	},
						    component: function (resolve) {
						      require(['../template/my/exportKey'], resolve)
						    },
				    },
				    //deleteWallet
				    {
					    	path: '/deleteWallet',
				      	name:'DeleteWallet',
				      	meta:{
				      		title:'删除钱包'
				      	},
						    component: function (resolve) {
						      require(['../template/my/deleteWallet'], resolve)
						    },
				    },
				    //addWallet
				    {
					    	path: '/addWallet',
				      	name:'AddWallet',
				      	meta:{
				      		title:'添加钱包'
				      	},
						    component: function (resolve) {
						      require(['../template/my/addWallet'], resolve)
						    },
				    },
				    //importWallet
				    {
					    	path: '/addImportWallet',
				      	name:'AddImportWallet',
				      	meta:{
				      		title:'导入钱包'
				      	},
						    component: function (resolve) {
						      require(['../template/my/addImportWallet'], resolve)
						    },
				    },
				    //importMnemonics
				    {
					    	path: '/importMnemonics',
				      	name:'ImportMnemonics',
				      	meta:{
				      		title:'导入助记词'
				      	},
						    component: function (resolve) {
						      require(['../template/my/importMnemonics'], resolve)
						    },
				    },
				    //importKey
				    {
					    	path: '/importKey',
				      	name:'ImportKey',
				      	meta:{
				      		title:'导入私钥'
				      	},
						    component: function (resolve) {
						      require(['../template/my/importKey'], resolve)
						    },
				    },
				    //importSuccess
				    {
					    	path: '/importSuccess',
				      	name:'ImportSuccess',
				      	meta:{
				      		title:'导入成功'
				      	},
						    component: function (resolve) {
						      require(['../template/my/importSuccess'], resolve)
						    },
				    },
				    //dealRecord
				    {
					    	path: '/dealRecord',
				      	name:'DealRecord',
				      	meta:{
				      		title:'账单'
				      	},
						    component: function (resolve) {
						      require(['../template/my/dealRecord'], resolve)
						    },
				    },
				    //Join the community
				    {
					    	path: '/joinCommunity',
				      	name:'JoinCommunity',
				      	meta:{
				      		title:'社群'
				      	},
						    component: function (resolve) {
						      require(['../template/my/joinCommunity'], resolve)
						    },
				    },
				    //helpCenter
				    {
					    	path: '/helpCenter',
				      	name:'HelpCenter',
				      	meta:{
				      		title:'帮助中心'
				      	},
						    component: function (resolve) {
						      require(['../template/my/helpCenter'], resolve)
						    },
				    },
				    //helpCenterDetails
				    {
					    	path: '/helpCenterDetails',
				      	name:'HelpCenterDetails',
				      	meta:{
				      		title:'详情'
				      	},
						    component: function (resolve) {
						      require(['../template/my/helpCenterDetails'], resolve)
						    },
				    },
				    //aboutUs
				    {
					    	path: '/aboutUs',
				      	name:'AboutUs',
				      	meta:{
				      		title:'关于我们'
				      	},
						    component: function (resolve) {
						      require(['../template/my/aboutUs'], resolve)
						    },
				    },
			    ]
		  },
  ]
})



