//全局指令
var directive ={
	install:function(Vue, installOptions) {
		Vue.directive('focus', {
		  // 当被绑定的元素插入到 DOM 中时……
		  inserted: function (el) {
		    // 聚焦元素
		    el.focus()
		  }
		});
		//日期格式化
		Vue.filter("formatDate", function(value,fmt) {
			if(value){
				let date = new Date(value*1000);
            	if (/(y+)/.test(fmt)) {
			        fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length));
			    }
			    let o = {
			        'M+': date.getMonth() + 1,
			        'd+': date.getDate(),
			        'h+': date.getHours(),
			        'm+': date.getMinutes(),
			        's+': date.getSeconds()
			    };
			    for (let k in o) {
			        if (new RegExp(`(${k})`).test(fmt)) {
			            let str = o[k] + '';
			            let padLeftZero = ('00' + str).substr(str.length)
			            fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? str : padLeftZero);
			        }
			    }
			    return fmt;
            }
        });
	}
}

module.exports = directive;


